<style lang="scss">
  .must {
    color: red
  }
  .baseExtendInfo{
    display: none
  }
</style>
<template>
  <div class="card card-block">
    <form class="form-horizontal j-forms">
      <h5> 录单员信息</h5>
      <div class="row form-group form-group-sm">
        <label class="col-md-1"><span class="must">*</span>录单员</label>
        <div class="col-md-2">
          <el-input v-model="base.c_opr_cde" id="base.c_opr_cde" readonly></el-input>
        </div>
        <label class="col-md-1"><span class="must">*</span>录单机构</label>
        <div class="col-md-2">
          <el-input v-model="base.c_dpt_cnm" id="base.c_dpt_cnm" readonly></el-input>
        </div>
        <label class="col-md-1">业务员</label>
        <div class="col-md-2">
          <el-input v-model="base.c_sls_nme" id="base.c_sls_nme" readonly></el-input>
        </div>
        <label class="col-md-1">业务员职业证号</label>
        <div class="col-md-2">
          <el-input v-model="base.c_sls_cde" id="base.c_sls_cde" readonly></el-input>
        </div>
      </div>
      <div class="row form-group form-group-sm">
        <label class="col-md-1"><span class="must">*</span>代理人</label>
        <div class="col-md-2">
          <el-input v-model="base.c_brkr_nme" id="base.c_brkr_nme" readonly></el-input>
        </div>
        <label class="col-md-1"><span class="must">*</span>代理协议</label>
        <div class="col-md-2">
          <el-select v-model="base.c_agt_agr_no" id="base.c_agt_agr_no" clearable placeholder="请选择">
            <el-option v-for="item in codelist.c_agt_agr_no" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </div>
        <label class="col-md-1">代理业务员</label>
        <div class="col-md-2">
          <el-input v-model="base.c_sales_cme" id="base.c_sales_cme" readonly></el-input>
        </div>
        <label class="col-md-1">销售人员职业证号</label>
        <div class="col-md-2">
          <el-input v-model="base.c_certf_no" id="base.c_certf_no" readonly></el-input>
        </div>
      </div>
      <div class="row form-group form-group-sm">
        <label class="col-md-1"><span class="must">*</span>保单归属地（地市）</label>
        <div class="col-md-2">
          <el-input v-model="base.c_area_cnm" id="base.c_area_cnm" readonly></el-input>
        </div>
        <div class="col-md-9" style="text-align:right">
          <el-button type="primary" @click.native="expand" value="0" target=".baseExtendInfo">
            展开
          </el-button>
        </div>
      </div>
      <div class="row form-group form-group-sm baseExtendInfo">
        <label class="col-md-1"><span class="must">*</span>涉农标志</label>
        <div class="col-md-2">
          <el-switch v-model="base.c_agri_mrk" name="base.c_agri_mrk" on-text="是" off-text="否" on-color="#13ce66" off-color="#ff4949">
          </el-switch>
        </div>
        <label class="col-md-1"><span class="must">*</span>争议处理</label>
        <div class="col-md-2">
          <el-select v-model="base.c_dispt_sttl_cde" id="base.c_dispt_sttl_cde" clearable placeholder="请选择">
            <el-option v-for="item in codelist.c_dispt_sttl_cde" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </div>
        <label class="col-md-1"><span class="must">*</span>仲裁名称</label>
        <div class="col-md-2">
          <el-input></el-input>
        </div>
        <label class="col-md-1">是否第三方业务</label>
        <div class="col-md-2">
          <el-select v-model="base.c_third_business" id="base.c_third_business" clearable placeholder="请选择">
            <el-option v-for="item in codelist.c_third_business" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </div>
      </div>
      <div class="row form-group form-group-sm baseExtendInfo">
        <label class="col-md-1"><span class="must">*</span>缴费方式</label>
        <div class="col-md-2">
          <el-select v-model="base.c_fin_typ" id="base.c_fin_typ" clearable placeholder="请选择">
            <el-option v-for="item in codelist.c_fin_typ" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </div>
        <label class="col-md-1">纯风险保费标识</label>
        <div class="col-md-2">
          <el-select v-model="base.c_pure_prm_flag" id="base.c_pure_prm_flag" clearable placeholder="请选择">
            <el-option v-for="item in codelist.c_pure_prm_flag" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </div>
        <label class="col-md-1">计费方式</label>
        <div class="col-md-2">
          <el-select v-model="base.c_ratio_typ" id="base.c_ratio_typ" clearable placeholder="请选择">
            <el-option v-for="item in codelist.c_ratio_typ" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </div>
        <label class="col-md-1"><span class="must">*</span>备案代码</label>
        <div class="col-md-2">
          <el-input v-model="base.c_record_code"></el-input>
        </div>
      </div>
      <div class="row form-group form-group-sm baseExtendInfo">
        <label class="col-md-1">交强险投保单号</label>
        <div class="col-md-2">
          <el-input v-model="base.jq_c_app_no" readonly></el-input>
        </div>
        <label class="col-md-1">商业险投保单号</label>
        <div class="col-md-2">
          <el-input v-model="base.sy_c_app_no" readonly></el-input>
        </div>
        <label class="col-md-1">交强险保单号</label>
        <div class="col-md-2">
          <el-input v-model="base.jq_c_ply_no" readonly></el-input>
        </div>
        <label class="col-md-1 control-label" ms-skip="true">商业险保单号</label>
        <div class="col-md-2">
          <el-input v-model="base.sy_c_ply_no" readonly></el-input>
        </div>
      </div>
      <div class="row form-group form-group-sm baseExtendInfo">
        <label class="col-md-1">商业险上年保单号</label>
        <div class="col-md-2">
          <el-input v-model="base.sy_c_orig_ply_no"></el-input>
        </div>
        <label class="col-md-1">交强险上年保单号</label>
        <div class="col-md-2">
          <el-input v-model="base.jq_c_orig_ply_no"></el-input>
        </div>
        <label class="col-md-1">业务员所属团队</label>
        <div class="col-md-2">
          <el-input v-model="base.c_salegrp_nme" readonly></el-input>
        </div>
      </div>
    </form>
  </div>
</template>
<script>
import ElSelect from 'components/select';
import ElOption from 'components/option';
import ElInput from 'components/input';
import ElButton from 'components/button';
import ElTag from 'components/tag';
import ElSwitch from 'components/switch'
import Tooltip from 'components/tooltip';
import PolicyUtil from '../util/PolicyUtil';
export default {
  name: 'policy-base',
  props:{},
  components:{
    ElSelect, 
    ElOption, 
    ElInput, 
    ElSwitch,
    ElButton, 
    ElTag, 
    Tooltip
  },
  data () {
    return {
      "base" : {
        "c_opr_cde" : "4400003",
        "c_dpt_cnm" : "广东分公司",
        "c_sls_nme" : "刘德华",
        "c_sls_cde" : "9527",
        "c_brkr_nme" : "郭富城",
        "c_agt_agr_no" : "002",
        "c_sales_cme" : "张学友",
        "c_certf_no" : "5354",
        "c_area_cnm" : "西伯利亚",
        "c_agri_mrk" : false,
        "c_third_business" : "0",
        "c_dispt_sttl_cde" : "0",
        "c_fin_typ" : "1",
        "c_pure_prm_flag" : "",
        "c_ratio_typ" : "0",
        "jq_c_app_no" : "1357",
        "jq_c_ply_no" : "2468",
        "sy_c_app_no" : "abcd",
        "sy_c_ply_no" : "wxyz",
        "jq_c_orig_ply_no" : "8642",
        "sy_c_orig_ply_no" : "zyxw"
      },
      "codelist" : {
        "c_agt_agr_no" : [
          {"value" : "001", "label" : "协议1"},
          { "value" : "002", "label" : "协议2" },
          { "value" : "003", "label" : "协议3" }
        ],
        "c_third_business" : [
          { "value" : "0", "label" : "否" },
          { "value" : "1", "label" : "是" }
        ],
        "c_dispt_sttl_cde" : [
          { "value" : "0", "label" : "依法向人民法院起诉" },
          { "value" : "1", "label" : "提交仲裁委员会" }
        ],
        "c_fin_typ" : [
          { "value" : "1", "label" : "刷卡缴费" },
          { "value" : "2", "label" : "支票缴费" },
          { "value" : "3", "label" : "转账缴费" }
        ],
        "c_pure_prm_flag" : [
          { "value" : "1", "label" : "标准车型保费" },
          { "value" : "2", "label" : "标准车型非标准保费" },
          { "value" : "3", "label" : "非标准车型保费" }
        ],
        "c_ratio_typ" : [
          { "value" : "0", "label" : "按日" }
        ]
      }
    }
  },

  "methods" : {
    expand(event){
     PolicyUtil.expand(event);
    }
  }
}
</script>


