                <div class="col-md-2">
                    <el-select v-model="vhl.c_reg_vhl_typ" clearable placeholder="请选择">
                        <el-option v-for="item in codelist.c_reg_vhl_typ" :label="item.label" :value="item.value"></el-option>
                    </el-select>
