<style lang="scss">
  .must {
    color: red
  }
  
  .insuredExtendInfo {
    display: none
  }
</style>
<template>
  <div class="card card-block">
    <form class="form-horizontal j-forms">
      <h5>被保人信息</h5>
      <div class="row form-group form-group-sm">
        <label class="col-md-1"><span class="must">*</span>被保人姓名</label>
        <div class="col-md-2">
          <el-input v-model="insured.c_insured_nme"></el-input>
        </div>
        <label class="col-md-1"><span class="must">*</span>被保人性质</label>
        <div class="col-md-2">
          <el-select v-model="insured.c_clnt_mrk" id="insured.c_clnt_mrk" clearable placeholder="请选择">
            <el-option v-for="item in codelist.c_clnt_mrk" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </div>
        <label class="col-md-1"><span class="must">*</span>联系人</label>
        <div class="col-md-2">
          <el-input v-model="insured.c_cntr_nme">
        </div>
        <label class="col-md-1"><span class="must">*</span>手机</label>
        <div class="col-md-2">
          <el-input v-model="insured.c_mobile">
        </div>
      </div>
      <div class="row form-group form-group-sm">
        <label class="col-md-1"><span class="must">*</span>证件类型</label>
        <div class="col-md-2">
          <el-select v-model="insured.c_certf_cls" clearable placeholder="请选择">
            <el-option v-for="item in codelist['c_certf_cls'][insured.c_clnt_mrk]" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </div>
        <label class="col-md-1"><span class="must">*</span>证件号</label>
        <div class="col-md-2">
          <el-input v-model="insured.c_certf_cde">
        </div>
        <div class="iCertfApp">
          <label class="col-md-1"><span class="must">*</span>性别</label>
          <div class="col-md-2">
            <el-select v-model="insured.c_sex" clearable placeholder="请选择">
              <el-option v-for="item in codelist.c_sex" :label="item.label" :value="item.value"></el-option>
            </el-select>
          </div>
          <label class="col-md-1"><span class="must">*</span>出生日期</label>
          <div class="col-md-2">
            <el-date-picker v-model="insured.t_birthday" type="date" placeholder="选择日期">
            </el-date-picker>
          </div>
        </div>
      </div>
      <div class="row form-group form-group-sm">
        <label class="col-md-1"><span class="must">*</span>被保人地址</label>
        <div class="col-md-2" id="addressIns">
          <!--
          <div class="address">
            <el-input type="hidden" name="province" v-model="insured.c_province">
              <el-input type="hidden" name="city" v-model="insured.c_city">
                <el-input type="hidden" name="area" v-model="insured.c_county">
          </div>-->
        </div>
        <label class="col-md-1"><span class="must">*</span>详细地址</label>
        <div class="col-md-2">
          <el-input v-model="insured.c_clnt_addr">
        </div>
        <label class="col-md-1"><span class="must">*</span>被保人类别</label>
        <div class="col-md-2">
          <el-select v-model="insured.c_resv_txt_20" clearable placeholder="请选择">
            <el-option v-for="item in codelist.c_resv_txt_20" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </div>
        <div class="col-md-3" style="text-align:right">
          <el-button type="primary" @click.native="expand" value="0" target=".insuredExtendInfo">
            展开
          </el-button>
        </div>
      </div>

      <div class="row form-group form-group-sm insuredExtendInfo">
        <label class="col-md-1">固定电话</label>
        <div class="col-md-2">
          <el-input v-model="insured.c_tel">
        </div>
        <label class="col-md-1">客户等级</label>
        <div class="col-md-2">
          <el-select v-model="insured.c_cus_lvl" clearable placeholder="请选择">
            <el-option v-for="item in codelist.c_cus_lvl" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </div>
        <label class="col-md-1">反洗钱客户风险等级</label>
        <div class="col-md-2">
          <el-select v-model="insured.c_resv_txt_4" clearable placeholder="请选择">
            <el-option v-for="item in codelist.c_resv_txt_4" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </div>
        <div class="col-md-2">
          <el-select v-model="insured.c_cust_risk_rank" clearable placeholder="请选择">
            <el-option v-for="item in codelist['c_cust_risk_rank'][insured.c_resv_txt_4]" :label="item.label" :value="item.value"></el-option>
          </el-select>
        </div>
      </div>

