        ],
        "c_certf_cls" : {
          "1" : [
            {"value" : "120001","label" : "居民身份证"},
            {"value" : "120002","label" : "护照"},
            {"value" : "120003","label" : "军人证"},
            {"value" : "120009","label" : "其他"}
          ],
          "2" : [
            {"value" : "110001","label" : "组织机构代码证"},
            {"value" : "110002","label" : "工商注册号码"},
            {"value" : "110003","label" : "营业执照"},
            {"value" : "110009","label" : "其它"}
          ],
          "3" : [
