		        {"value" : "915205","label" : "洗钱类负面报道"},
		        {"value" : "915206","label" : "反洗钱监控地区"},
		        {"value" : "915207","label" : "拒绝开展尽职调查"},
		        {"value" : "915208","label" : "可疑情形"}
          ]
        },
        "c_resv_txt_20" : [
          {"value" : "1", "label" : "车辆所有人" }, 
          {"value" : "2", "label" : "车辆管理人" }, 
          {"value" : "3", "label" : "被保险人允许的合法驾驶员" }, 
          {"value" : "9", "label" : "其他" }
        ]
      }
    }
  },
  "methods" : {
    expand(event){
      PolicyUtil.expand(event);
    },
    resetCustomer(){
      var insured = this._data.insured;
      PolicyUtil.resetModel(insured);
    },
    searchCustomer(){
      var insured = this._data.insured;
      PolicyUtil.searchCustomer(insured);
    }
